"use client";

import { FileText } from "lucide-react";
import { useLatex } from "@/lib/LatexContext";
import 'katex/dist/katex.min.css'
import { unified } from 'unified'
import remarkParse from 'remark-parse'
import remarkMath from 'remark-math'
import rehypeKatex from 'rehype-katex'
import rehypeReact from 'rehype-react'
import React from "react";


export default function Pdf() {
  const { latexContent } = useLatex();
  const [processedContent, setProcessedContent] = React.useState<React.ReactNode>(null);
const markdownWithMath = `
This is inline math: $E=mc^2$.

This is display math:

$$
\\int_0^\\infty e^{-x} dx = 1
$$
    `;
  React.useEffect(() => {
    if (latexContent) {
      unified()
        .use(remarkParse)
        .use(remarkMath)
        .use(rehypeKatex)
        .use(rehypeReact, { createElement: React.createElement })
        // .process(latexContent)
        .process(`
Hello, this is some text with math:

Inline math: $E=mc^2$

Display math:

$$
\int_0^\infty e^{-x} dx = 1
$$
`)

        .then((file: any) => {
          setProcessedContent(file.result);
        });
    }
  }, [latexContent]);

  return (
    <>
      {
        !latexContent ?
      <div className="p-4">
        {processedContent}
        
      </div> 
      :
      <div className="bg-slate-200 h-full p-4">
        <div className="border-2 border-gray-300 h-full rounded-xl overflow-hidden ">
          <div className="flex flex-col justify-center items-center h-full text-slate-500">
            <FileText className="h-10 w-10" />
            <p className="text-2xl"> pdf here</p>
          </div>
        </div>
      </div>
      
      }

    </>
  )
}
